<template>
  <el-card>
    <el-row>
      <!-- <el-col :span="8" class="df mb10">
        <el-input placeholder="请输入内容">
          <el-button slot="append" icon="el-icon-search">搜索</el-button>
        </el-input>
      </el-col>-->
      <el-col>
        <el-table :data="tableData" stripe style="width: 100%" max-height="550px" border>
          <el-table-column prop="userName" label="操作人" header-align="center" align="center"></el-table-column>
          <el-table-column prop="ip" label="ip地址" header-align="center" align="center"></el-table-column>
          <el-table-column label="操作时间" header-align="center" align="center">
            <template slot-scope="scope">{{scope.row.time | formatDate}}</template>
          </el-table-column>
          <el-table-column prop="action" label="操作内容" header-align="center" align="center"></el-table-column>
        </el-table>
      </el-col>

      <el-col class="mt10 df jcfe">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="current_page"
          :page-sizes="[50, 100, 200]"
          :page-size="page_size"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          background
        ></el-pagination>
      </el-col>
    </el-row>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      current_page: 1, //当前选择的第几页
      total: 1, //总共有多少条数据
      page_size: 50, //当前一页选择的条数
      filters_type: [],
      tableData: []
    };
  },
  mounted() {
    this.get_log_list();
  },
  methods: {
    handleCurrentChange(res) {
      this.current_page = res;
      this.get_log_list();
    },
    handleSizeChange(res) {
      this.page_size = res;
      this.get_log_list();
    },
    get_log_list() {
      this.$get("log/systemLog", {
        param: {
          current_page: this.current_page,
          page_size: this.page_size
        }
      }).then(res => {
        this.tableData = res.data;
        this.total = res.total;
      });
    }
  }
};
</script>

<style scoped>
</style>


